import { Sprite } from '@pixi/react';
import React from 'react';
const path = '../assets/Cards/';
export const Card = ({ isBack, card, position, zIndex }) => (React.createElement(Sprite, { image: isBack ? `${path}back/card_back.png` : `${path}${card.suit}/${card.value}_${card.suit}.png`, position: position, roundPixels: true, anchor: 0.5, scale: 0.7, zIndex: zIndex || 1 }));
