import React from 'react';
import { Sprite } from '@pixi/react';
import { DropShadowFilter } from '@pixi/filter-drop-shadow';
const path = '../../assets/Coins/';
const filter = new DropShadowFilter();
filter.color = 0xffffff;
export const Coin = ({ bet, position, isSelected, onSelect }) => {
    return (React.createElement(Sprite, { image: `${path}${bet}.png`, position: position || { x: 0, y: 0 }, scale: 0.5, interactive: true, onclick: () => onSelect(bet), anchor: 0.5 }));
};
